<template>
  <div>
    <!--  -->
     <el-breadcrumb separator-class="el-icon-arrow-right">
      <el-breadcrumb-item :to="{ path: '/home' }"></el-breadcrumb-item>
      <el-breadcrumb-item></el-breadcrumb-item>
      <el-breadcrumb-item></el-breadcrumb-item>
    </el-breadcrumb>
    <!--  -->
    <el-card>
      <el-row :gutter="20">
        <el-col :span="7">
          <el-input placeholder="" v-model="searchInput" class="input-with-select" closed>
            <template #append>
              <el-button icon="el-icon-search" @click="searchContent"></el-button>
            </template>
          </el-input>
        </el-col>
      </el-row>
      <!--  -->
      <el-table :data="orderList" style="width: 100%" border>
        <el-table-column type="index" label="#"></el-table-column>
        <el-table-column prop="order_number" label=""></el-table-column>
        <el-table-column prop="order_price" label=""></el-table-column>
        <el-table-column prop="pay_status" label="">
          <template slot-scope="scope">
             <el-tag type="success" v-if="scope.row.order_pay === '1'"></el-tag>
             <el-tag type="danger" v-else></el-tag>
          </template>
        </el-table-column>
        <el-table-column prop="is_send" label=""></el-table-column>
        <el-table-column prop="create_time" label="">
          <template slot-scope="scope">
            {{scope.row.create_time | dateFormat}}
          </template>
        </el-table-column>
        <el-table-column label="" width="125px">
          <template slot-scope="">
            <el-button type="primary" icon="el-icon-edit" size="mini" @click="showEditAddress"></el-button>
            <el-button type="success" icon="el-icon-location" size="mini" @click="showProgress"></el-button>
          </template>
        </el-table-column>
      </el-table>
      <!--  -->
      <el-pagination
        @size-change="handleSizeChange"
        @current-change="handleCurrentChange"
        :current-page="queryInfo.pagenum"
        :page-sizes="[5, 10, 20, 30]"
        :page-size="queryInfo.pagesize"
        layout="total, sizes, prev, pager, next, jumper"
        :total="total"
      >
      </el-pagination>
      <!--  -->
      <el-dialog
        title=""
        :visible.sync="editDialogVisible"
        width="50%"
        @close="addressDialogClosed"
      >
        <el-form :model="addressForm" ref="addressFormRef" :rules="addFormRules" label-width="100px">
          <el-form-item label="/" prop="address1">
            <el-cascader
              style="width:100%"
              :options="cityData"
              v-model="addressForm.address1"
            >
            </el-cascader>
          </el-form-item>
          <el-form-item label="" prop="address2">
            <el-input v-model="addressForm.address2"></el-input>
          </el-form-item>
        </el-form>
        <template #footer>
          <span class="dialog-footer">
            <el-button @click="editDialogVisible = false"> </el-button>
            <el-button type="primary" @click="editDialogVisible = false"> </el-button>
          </span>
        </template>
      </el-dialog>
      <!--  -->
      <el-dialog
        title=""
        :visible.sync="progressDialogVisible"
        width="50%"
      >
        <el-timeline>
          <el-timeline-item
            v-for="(activity, index) in progressInfo"
            :key="index"
            :timestamp="activity.time"
          >
            {{activity.context}}
          </el-timeline-item>
        </el-timeline>
        <template #footer>
          <span class="dialog-footer">
            <el-button @click="progressDialogVisible = false"> </el-button>
            <el-button type="primary" @click="progressDialogVisible = false"> </el-button>
          </span>
        </template>
      </el-dialog>
    </el-card>
  </div>
</template>

<script>
// 
import cityData from './citydata'
export default {
  name: 'Order',
  data () {
    return {
      // 
      queryInfo: {
        pagenum: 1,
        pagesize: 5
      },
      // 
      total: 0,
      // 
      orderList: [],
      // 
      editDialogVisible: false,
      // 
      addressForm: {
        address1: [],
        address2: ''
      },
      // 
      cityData: cityData,
      // 
      addFormRules: {
        address1: [
          { required: true, message: '', trigger: 'blur' }
        ],
        address2: [
          { required: true, message: '', trigger: 'blur' }
        ]
      },
      // 
      progressDialogVisible: false,
      // 
      progressInfo: [],
      // 
      searchInput: ''
    }
  },
  created () {
    //  
    this.getOrderList()
  },
  methods: {
    // 
    async getOrderList () {
      const { data: res } = await this.$http.get('orders', { params: this.queryInfo })
      if (res.meta.status !== 200) return this.$message.error('')
      console.log(res.data)
      this.total = res.data.total
      this.orderList = res.data.goods
    },
    handleSizeChange (newSize) {
      // 
      this.queryInfo.pagesize = newSize
      this.getOrderList()
    },
    handleCurrentChange (newPage) {
      // 
      this.queryInfo.pagenum = newPage
      this.getOrderList()
    },
    // 
    showEditAddress () {
      this.editDialogVisible = true
    },
    // 
    addressDialogClosed () {
      this.$refs.addressFormRef.resetFields()
    },
    // 
    async showProgress () {
      const { data: res } = await this.$http.get('/kuaidi/1106975712662')
      // console.log(res)
      if (res.meta.status !== 200) return this.$message.error('')
      this.progressInfo = res.data
      this.progressDialogVisible = true
    },
    // 
    searchContent () {
      console.log(123)
    }
  }
}
</script>

